import { onMounted, ref } from "vue";

export default {
  name: "Heart",
  setup() {
    var heart = ref(null);
    onMounted(() => {
      var h = document.querySelector(".heart-wrapper");
      function toggleActivate() {
        h.classList.toggle("active");
        h.removeEventListener("click", toggleActivate);
        heart.value.style.background = "transparent";
        heart.value.style.opacity = 0;
        heart.value.style.zIndex = "888";
      }
      h.addEventListener("click", toggleActivate, false);
    });
    return {
      heart,
    };
  },
};
