.heart-wrapper {
  height: 30em;
  width: 30em;
  position: relative;
  cursor: pointer;
}
.heart-wrapper .heart {
  display: block;
  height: 10em;
  width: 10em;
  top: 54%;
  margin-top: -5em;
  margin-left: -5em;
  left: 50%;
  position: absolute;
  z-index: 0;
}
.heart-wrapper .heart > * {
  overflow: hidden;
  position: absolute;
}
.heart-wrapper .heart > *:after {
  display: block;
  content: "";
  position: absolute;
  background: #aab8c2;
}
.heart-wrapper .heart .tl {
  height: 25%;
  width: 50.25%;
  top: 0;
  left: 0;
}
.heart-wrapper .heart .tl:after {
  top: 0;
  left: 0;
  height: 200%;
  width: 104%;
  border-top-left-radius: 70% 80%;
  border-top-right-radius: 80% 80%;
  border-bottom-right-radius: 50% 50%;
  border-bottom-left-radius: 50% 50%;
}
.heart-wrapper .heart .tr {
  height: 25%;
  width: 50.25%;
  top: 0;
  right: 0;
}
.heart-wrapper .heart .tr:after {
  top: 0;
  right: 0;
  height: 200%;
  width: 104%;
  border-top-left-radius: 80% 80%;
  border-top-right-radius: 70% 80%;
  border-bottom-right-radius: 50% 50%;
  border-bottom-left-radius: 50% 50%;
}
.heart-wrapper .heart .bl {
  height: 75.5%;
  width: 50.25%;
  bottom: 0;
  left: 0;
}
.heart-wrapper .heart .bl:after {
  top: 0;
  left: 0;
  height: 95%;
  width: 160%;
  border-top-left-radius: 0 0;
  border-top-right-radius: 0 0;
  border-bottom-right-radius: 0 0;
  border-bottom-left-radius: 100% 100%;
}
.heart-wrapper .heart .br {
  height: 75.5%;
  width: 50.25%;
  bottom: 0;
  right: 0;
}
.heart-wrapper .heart .br:after {
  top: 0;
  right: 0;
  height: 95%;
  width: 160%;
  border-top-left-radius: 0 0;
  border-top-right-radius: 0 0;
  border-bottom-right-radius: 100% 100%;
  border-bottom-left-radius: 0 0;
}
.heart-wrapper .ring {
  display: block;
  position: absolute;
  border-width: 0;
  border-style: solid;
  top: 50%;
  left: 50%;
  border-radius: 50%;
  z-index: 1;
}
.heart-wrapper .circles {
  display: block;
  position: absolute;
  height: 1.66667em;
  width: 1.66667em;
  top: 50%;
  left: 50%;
  margin-top: -0.83333em;
  margin-left: -0.83333em;
  z-index: 2;
  border-radius: 50%;
}
.heart-wrapper:hover .heart .tl:after,
.heart-wrapper:hover .heart .tr:after,
.heart-wrapper:hover .heart .bl:after,
.heart-wrapper:hover .heart .br:after {
  background: #e2264d;
}
.heart-wrapper.active .heart {
  -webkit-animation-name: heart;
  animation-name: heart;
  -webkit-animation-duration: 1.2s;
  animation-duration: 1.2s;
  -webkit-animation-timing-function: ease-in;
  animation-timing-function: ease-in;
  -webkit-animation-iteration-count: infinite;
  animation-iteration-count: infinite;
}
